$lightBG: rgba(243, 202, 131, 0.36);
$darkBG: rgba(248, 159, 5, 0.36);
$markColor: #291919;

.tile {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 11.11%;
  height: 11.11%;
  flex-grow: 1;
  // https://stackoverflow.com/questions/17915865/how-to-make-borders-collapse-on-a-div
  box-shadow:
    2px 0 0 0 $lightBG,
    0 2px 0 0 $lightBG,
    2px 2px 0 0 $lightBG,
    /* Just to fix the corner */
    2px 0 0 0 $lightBG inset,
    0 2px 0 0 $lightBG inset;
}

.tile-chosen {
  color: #0506fb;
  font-size: 30px;
}

