.tile-F3,
.tile-D7,
.tile-D8,
.tile-D9,
.tile-E7,
.tile-E8,
.tile-E9,
.tile-F7,
.tile-F8,
.tile-F9,
.tile-G4,
.tile-G5,
.tile-G6,
.tile-H4,
.tile-H5,
.tile-H6,
.tile-I4,
.tile-I5,
.tile-I6 {
  background: $darkBG;
  .mark-mode & {
    background: $lightBG;
  }
}

.tile-marked {
  flex-wrap: wrap;

  &>div {
    flex-basis: 33.3%;
    height: 33.3%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: transparent;
    font-size: 10px;
  }

  &.tile-mark-mode:hover>div {
    cursor: pointer;
    color: $markColor;
    box-shadow:
      1px 0 0 0 $lightBG,
      0 1px 0 0 $lightBG,
      1px 1px 0 0 $lightBG,
      /* Just to fix the corner */
      1px 0 0 0 $lightBG inset,
      0 1px 0 0 $lightBG inset;

    &:hover {
      color: red;
    }
  }

  &>div.marked {
    color: red;
  }

}

.tile-flagged {
    background-color: #eeffee;
}